.post {
  max-width: 500px;
  background-color: white;
  border: 1px solid lightgray;
  margin-bottom: 45px;
  border-radius: 20px;
}
.post__image {
  width: 100%;
  object-fit: contain;
  border-top: 1px solid lightgray;
  border-bottom: 1px solid lightgray;
}
.post__text {
  font-weight: normal;
  padding: 5px 10px;
  margin: 10px;
}
.post__header {
  display: flex;
  align-items: center;
  padding: 20px;
}
.post__avatar {
  margin-right: 10px;
}
.post__commentBox{
  display: flex;
  margin-top: 10px;
}
.post__commentBox input{
  border-radius: 0 0 0 20px;
}
.post__comments{
  padding: 20px;
}
.post__comments h4:hover{
  cursor: pointer;
}
.post__input{
  flex:1;
  border: none;
  padding: 10px;
  border-top: 1px solid lightgray;
}
.post__button{
  flex:0;
  color: #6082a3;
  border: none;
  border-top: 1px solid lightgray;
  border-radius: 0 0 20px 0;
}
